import React, { useEffect, useState } from "react";
import InputBox from "../InputBox";
import OutputBox from "../OutputBox";
import ActionButtons from "../ActionButtons";
import { Spinner, Text, Message } from "@innovaccer/design-system";
import { getEHRData } from "../AIResponse";

const Disclaimer = () => {
  return (
    <Text
      size="large"
      appearance="subtle"
      className=" mt-6 mb-9 d-flex justify-content-center"
    >
      All data is securely transmitted and processed. We do not store any PHI
      information.
    </Text>
  );
};

const ErrorTemplate = () => {
  return (
    <Message
      appearance="alert"
      data-test="Docs-propTable-error"
      className="my-7 Error-template"
      title="We are facing error while fetching the EHR notes currently"
      description="Hold tight, we are working to get it up for you to interact with."
    />
  );
};

const APIResult = ({ showError, showLoader }) => {
  useEffect(() => {
    const element = document.getElementById("error-container");
    element.scrollIntoView({ behavior: "smooth" });
  });

  return (
    <div id="error-container" className="m-4">
      {showLoader && (
        <div>
          <Spinner />
        </div>
      )}

      {showError && <ErrorTemplate />}
    </div>
  );
};

const EHRNotes = () => {
  const [showSampleNote, setShowSampleNote] = useState(false);
  const [showLoader, setShowLoader] = useState(false);
  const [showError, setShowError] = useState(false);
  const [AINotes, setAINotes] = useState("");
  const [query, setQuery] = useState("");

  const sampleNote =
    "create a sample EHR note for James Lawson, 53 years old, with risk of heart disease and diabetes";

  const onQueryChange = (e) => {
    setQuery(e.target.value);
  };

  const sampleClickHandler = () => {
    setShowSampleNote(true);
    setQuery(sampleNote);
  };

  const getAIResponse = (query) => {
    setShowLoader(true);
    setShowError(false);
    getEHRData(query)
      .then((data) => {
        setShowLoader(false);
        console.log("data-> ", data);

        if (data) {
          setAINotes(data);
          return data;
        } else {
          setShowError(true);
        }
      })
      .catch(function (error) {
        setShowLoader(false);
        setShowError(true);
        console.log("We will facing error while fetching the data", error);
      });
  };

  const generateAINote = () => {
    if (query === sampleNote || query === "") {
      return;
    }
    setShowSampleNote(false);

    // update this value with the code generated by Lambda Function
    getAIResponse(query);
  };

  return (
    <div className="d-flex justify-content-center flex-column mt-9">
      <div className="bg-secondary-lightest p-6">
        <InputBox value={query} onQueryChange={onQueryChange} />
        <ActionButtons
          generateSample={sampleClickHandler}
          generateAINote={generateAINote}
        />
      </div>

      <Disclaimer />
      {!showSampleNote && (
        <APIResult showError={showError} showLoader={showLoader} />
      )}

      {(showSampleNote || AINotes !== "") && !showLoader ? (
        <OutputBox showSampleNote={showSampleNote} AINotes={AINotes} />
      ) : (
        ""
      )}
    </div>
  );
};

export default EHRNotes;
