import React, { useEffect } from "react";
import SampleData from "./SampleData";
import CopyToClipboard from "../CopyToClipboard";
import { Heading } from "@innovaccer/design-system";

const OutputNote = ({ showSampleNote, AINotes }) => {
  return (
    <div id="output-note-content">
      {showSampleNote ? <SampleData /> : <div className="mt-6">{AINotes}</div>}
    </div>
  );
};

const OutputBox = ({ showSampleNote, AINotes }) => {
  useEffect(() => {
    const element = document.getElementById("outputBox");
    element.scrollIntoView({ behavior: "smooth" });
  });

  return (
    <div id="outputBox" className="bg-secondary-lightest px-6 pt-6 pb-7 mb-9">
      <div className="d-flex align-items-center justify-content-between">
        <Heading size="s">Generated note</Heading>

        <div className="d-flex justify-content-end">
          <CopyToClipboard />
        </div>
      </div>

      <div className="Output-note">
        <OutputNote showSampleNote={showSampleNote} AINotes={AINotes} />
      </div>
    </div>
  );
};

export default OutputBox;
